<template>
  <section class="DevInfo">
    <Header title="详细信息">
      <span class="iconfont icon_back" v-show="isLogin" slot="left" @click="_GoBack">&#xe61d;</span>
      <router-link to="/Mine"  v-show="isLogin" slot="right">
        <span class="iconfont icon_member">&#xe607;</span>
      </router-link>
    </Header>
    <section class="content">
      <Cell title="设备型号" :value="devInfo.dev_name"></Cell>
      <Cell title="维修人员" :value="devInfo.repairtor"></Cell>
      <Cell title="持有者姓名" :value="devInfo.username"></Cell>
      <Cell title="持有者电话" :value="devInfo.tel"></Cell>
      <Cell title="持有者学院" :value="devInfo.xueyuan"></Cell>
      <Cell title="持有者专业" :value="devInfo.zhuanye"></Cell>
      <Cell title="维修金额">
        <span class="c_red">{{devInfo.price == undefined ? '待定' : '￥' + devInfo.price}}</span>
      </Cell>
      <Cell title="维修开始时间" :value="devInfo.time_s"></Cell>
      <Cell title="问题描述" :value="devInfo.content"></Cell>
      <Cell title="当前状态">
        <span :class=" devInfo.status == 1 ? 'c_red' : 'c_green' ">{{devInfo.status == 1 ? '正在维修' : '已完成'}}</span>
      </Cell>
      <Cell  v-show="isLogin" title="打印二维码" is-link>
        <Button size="small" type="primary">打印</Button>
      </Cell>
       <Cell v-show="devInfo.status != 3 && isLogin " title="维修完成" is-link @click.native="_DevorderDone">
        <Button size="small" type="primary">确认</Button>
      </Cell>
    </section>
  </section>
</template>

<script>
import qs from 'qs';
import { Toast, Header, Cell, Button, MessageBox } from 'mint-ui';
export default {
  name: 'DevInfo',
  data () {
    return {
      id:　this.$route.params.id,
      devInfo: {},
      isLogin: true
    }
  },
  components: {
    Toast,
    Header,
    Cell,
    Button,
    MessageBox
  },
  watch: {
    '$route' (to, from) {
      this.id = this.$route.params.id;
        let data = {
        id: this.id
      };
      this.$axios.post('http://repaire.dnpuzi.com/Admin/index/showDevInfo', qs.stringify(data))
      .then(res => {
        if(res.data.code == 1) {
          this.devInfo = res.data.data
        }
      })
    }
  },
  mounted() {
    if(!localStorage.getItem('userInfo')) {
      this.isLogin = false;
    }
    let data = {
      id: this.id
    };
    this.$axios.post('http://repaire.dnpuzi.com/Admin/index/showDevInfo', qs.stringify(data))
    .then(res => {
      if(res.data.code == 1) {
        this.devInfo = res.data.data
      }
    })
  },
  methods: {
    _GoBack() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    },
    _DevorderDone() {
      if(JSON.parse(localStorage.getItem('userInfo')).id != this.devInfo.repairtor_id) {
        Toast({
          message: '你没有权限修改其他维修员的电脑信息',
          position: 'middle',
          duration: 1500
        });
        return;
      }
      MessageBox.prompt('请输入维修金额', {
        title: '',
        inputType: 'number'
      }).then(({value, action}) => {
        if(action == 'confirm' && value != null && value != '') {
          MessageBox({
            title: '维修金额：￥' + value,
            message: '确定完成?',
            showCancelButton: true
          }).then(action => {
            if(action == 'confirm') {
              let data = {
                id: this.id,
                price: value
              };
              this.$axios.post('http://repaire.dnpuzi.com/Admin/index/devOrderDone', qs.stringify(data))
              .then(res => {
                let data = {
                  id: this.id
                };
                this.$axios.post('http://repaire.dnpuzi.com/Admin/index/showDevInfo', qs.stringify(data))
                .then(res => {
                  if(res.data.code == 1) {
                    this.devInfo = res.data.data
                  }
                })
              });
            }
          })
        } else {
          Toast({
            message: '没有输入金额',
            position: 'middle',
            duration: 1500
          });
        }
      })
      .catch( () => {
        Toast({
          message: '已取消',
          position: 'middle',
          duration: 1500
        });
      } )
    }
  },
  beforeCreate() {
  }
}
</script>

<style scoped>
  .DevInfo {
    width: 100%;
    min-height: 100%;
  }
  .DevInfo .mint-header {
    background-color: #8aa5fa;
  }
  .icon_member {
    font-size: 24px;
  }
  .content {
    padding-bottom: 20px;
  }
</style>



